import React, { useState } from 'react'
import SliderWithText from '../components/Slider/SliderWithText'
import { createUseStyles } from "react-jss"
import TextField from "@material-ui/core/TextField/TextField"
import Typography from "@material-ui/core/Typography/Typography"
import Table from "@material-ui/core/Table/Table"
import TableRow from "@material-ui/core/TableRow/TableRow"
import TableCell from "@material-ui/core/TableCell/TableCell"
import TableBody from "@material-ui/core/TableBody/TableBody"
import TableHead from "@material-ui/core/TableHead/TableHead"
import SimpleLayout from "../components/SimpleLayout/SimpleLayout"
import { toLagsLaan } from "../utils/calculator"

const useStyles = createUseStyles({
  root: {
  },
  parameters: {
    background: 'whitesmoke',
    padding: '20px',
    borderRadius: '20px',
    borderStyle: 'solid',
    borderWidth: '1px',
    borderColor: 'lightgray',
  },
  sliderContainer: {
    alignItems: 'flex-end',
    display: 'flex',
  },
  bothLoans: {
    display: 'flex',
  },
  loanContainer: {
    marginRight: '50px',
    '&:last-child': {
      marginRight: '0px',
    },
    width: '100%',
    margin: '0px',
    display: 'flex',
    flexDirection: 'column',
  },
  proportion: {
    width: '50%',
  },
  input: {
    width: '4em',
  },
  paper: {
    margin: '20px',
  },
  table: {

  },
  tableContainer: {
    padding: '20px 20px 0px 20px',
  }
})

const LaanBeregner = (props) => {

  const classes = useStyles()
  
  const [proportion, setProportion] = useState(75)
  const [laanebeloeb, setLaanebeloeb] = useState(2000000)

  // Foerste laan
  const [kurs1, setKurs1] = useState(97.00)  
  const [rente1, setRente1] = useState(1)
  const [loebetid1, setLoebetid1] = useState(30)
  const [afdragsfri1, setAfdragsfri1] = useState(10)

  // Andet laan
  const [kurs2, setKurs2] = useState(99.00)  
  const [rente2, setRente2] = useState(1)
  const [loebetid2, setLoebetid2] = useState(30)
  const [afdragsfri2] = useState(0)

  // Beregn laan
  const laan1 = {
    rente: rente1 / 100,
    kurs: kurs1,
    terminer: loebetid1 * 4,
    afdragsfri: afdragsfri1 * 4,
  }

  const laan2 = {
    rente: rente2 / 100,
    kurs: kurs2,
    terminer: loebetid2 * 4,
    afdragsfri: afdragsfri2 * 4,
  }

  const toLagsLaanResult = toLagsLaan(laan1, laan2, laanebeloeb, proportion)

  const onSliderChange = handler => (event, value) => {
    handler(value)
  };

  const handleChange = handler => event => {
    handler(event.target.value)
  };

  return (
    <SimpleLayout>
    <div className={classes.root}>
      <div className={classes.parameters}>
        <TextField
          label={'Beløb til udbetaling'}
          type='number'
          variant="outlined"
          value={laanebeloeb}
          inputProps={{step: 50000}}    
          onChange={handleChange(setLaanebeloeb)}
        />
        <SliderWithText
          label={'Deling'}
          onChange={onSliderChange(setProportion)}
          min={0}
          max={100}
          step={1}
          value={proportion}
          className={classes.proportion}
        />
        <div className={classes.bothLoans}>
          <div className={classes.loanContainer}>
            <Typography>Lån 1</Typography>
            <TextField
              id="rente1"
              label="Rente"
              fullWidth={false}
              value={rente1}
              onChange={handleChange(setRente1)}
              inputProps={{step: 0.5}}
              margin="normal"
              type="number"
            />
            <TextField
              id="kurs1"
              label="Kurs"
              fullWidth={false}
              value={kurs1}
              onChange={handleChange(setKurs1)}
              margin="normal"
              type="number"
            />
            <SliderWithText
              label={'Løbetid'}
              onChange={onSliderChange(setLoebetid1)}
              min={0}
              max={30}
              step={10}
              value={loebetid1}
            />
            <SliderWithText
              label={'Afdragsfrihed'}
              onChange={onSliderChange(setAfdragsfri1)}
              min={0}
              max={10}
              step={1}
              value={afdragsfri1}
            />
          </div>
          <div className={classes.loanContainer}>
            <Typography>Lån 2</Typography>
            <TextField
              id="rente2"
              label="Rente"
              fullWidth={false}
              value={rente2}
              onChange={handleChange(setRente2)}
              inputProps={{step: 0.5}}
              margin="normal"
              type="number"
            />
            <TextField
              id="kurs2"
              label="Kurs"
              value={kurs2}
              onChange={handleChange(setKurs2)}
              margin="normal"
              type="number"
            />
            <SliderWithText
              label={'Løbetid'}
              onChange={onSliderChange(setLoebetid2)}
              min={0}
              max={30}
              step={10}
              value={loebetid2}
            />
          </div>
        </div>
      </div>
      <div className={classes.tableContainer}>
        <Typography variant={'h5'}>Nøgletal</Typography>
        <Table className={classes.table}>
          <TableBody>
            <TableRow>
              <TableCell component="th" scope="row">
                Hovedstol
              </TableCell>
              <TableCell numeric>{ Math.round(toLagsLaanResult.hovedstol) } kr.</TableCell>
            </TableRow>
            <TableRow>
              <TableCell component="th" scope="row">
                Ydelse pr. måned før skat
              </TableCell>
              <TableCell numeric>{ Math.round(toLagsLaanResult.tkYdelse) } kr.</TableCell>
            </TableRow>
            <TableRow>
              <TableCell component="th" scope="row">
                Ydelse pr. måned efter skat
              </TableCell>
              <TableCell numeric>{ Math.round(toLagsLaanResult.tkYdelseEfterSkat) } kr.</TableCell>
            </TableRow>
            <TableRow>
              <TableCell component="th" scope="row">
                Første måneds afdrag
              </TableCell>
              <TableCell numeric>{ Math.round(toLagsLaanResult.tkAfdrag) } kr.</TableCell>
            </TableRow>
            <TableRow>
              <TableCell component="th" scope="row">
                Rente og bidrag i lånets løbetid
              </TableCell>
              <TableCell numeric> { Math.round(toLagsLaanResult.tkRenteOgBidragTotal) } kr.</TableCell>
            </TableRow>
            <TableRow>
              <TableCell component="th" scope="row">
                Samlet beløb, der skal betales i lånets løbetid
              </TableCell>
              <TableCell numeric> { Math.round(toLagsLaanResult.tkSamletBeloeb)} kr.</TableCell>
            </TableRow>
            <TableRow>
              <TableCell component="th" scope="row">
                Bidragssats pr. år
              </TableCell>
              <TableCell numeric> { Math.round(toLagsLaanResult.bidragsSats * 100) / 100 } % </TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </div>
    </div>
    <div className={classes.tableContainer}>
      <Typography variant={'h5'}>Annuitets Tabel</Typography>
      <Table className={classes.table}>
        <TableHead>
          <TableRow>
            <TableCell>Kvartal</TableCell>
            <TableCell align="right">Afdrag</TableCell>
            <TableCell align="right">Restgæld</TableCell>
            <TableCell align="right">Ydelse</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>{ toLagsLaanResult.afdrag.map( (e,i) =>
            <TableRow>
              <TableCell component="th" scope="row">
                {i+1}.
              </TableCell>
              <TableCell align="right">
                { Math.round(toLagsLaanResult.afdrag[i])} kr.
              </TableCell>
              <TableCell align="right">
                { Math.round(toLagsLaanResult.restgaeld[i])} kr.
              </TableCell>
              <TableCell align="right">
                { Math.round(toLagsLaanResult.ydelse[i])} kr.
              </TableCell>
            </TableRow>
        )}
        </TableBody>
      </Table>
    </div>
    </SimpleLayout>
  )
}


export default LaanBeregner
